.swiper{
    position: absolute;
    display: grid;
    top: 50%;
    width: 100%;
    height: 50%;
    grid-template-rows: 80% 20%;
    grid-template-columns: 1fr;
    align-content: center;
}
.swiper-wrapper {
    overflow-y: visible;
    overflow-x: visible;
}
.swiper-slide{
    display: flex;
    justify-content: center;
    align-items: center;
}
.swiper-pagination-progressbar{
    grid-row: 2/2;
    justify-self: center;
    left: 25% !important;
    top: 5px !important;
    width: 50% !important;
    background: #292929;
}
.swiper-pagination-progressbar-fill{
    background: #4B4B4B !important;
}
.swiper-slide-active>div {
    opacity: 1;
    transition: all 0.3s;
    transition-delay: 0.1s;
}

.swiper-slide-next>div {
    opacity: 0;
}

.swiper-slide-prev>div {
    opacity: 0;
}