.loader{
    position: absolute;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 3;
}
.units{
    width: 130px;
    height: 70px;
    display: flex;
    align-items: flex-end;
    animation: load 2s infinite;
}
.big{
    width: 70px;
    height: 70px;
    border-radius: 50%;
    background-color: #6CFF95;
}
.small{
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: #419F6D;
}
@keyframes load{
    from{
        transform: rotate(0deg);
    }
    to{
        transform: rotate(360deg);
    }
}
.hide{
    transition: all 0.3s;
    opacity: 0;
}