.grid{
    position: absolute;
    top: 5px;
    left: 5px;
    width: calc(100% - 10px);
    display: grid;
    grid-template-rows: 50px;
    grid-template-columns: 10% 90%;
}
.grid *{
    align-self: center;
    justify-self: center;
}
.routes{
    display: flex;
    width: 100%;
    height: auto;
    justify-content: space-evenly;
}