@media screen and (min-width: 320px) {
    @media screen and (min-height: 620px){
        .container, .box{
            height: 400px;
        }
        #metaball{
            height: 400px;
            width: 400px;
        }
     }
    @media screen and (min-height: 820px){
        .container, .box{
            height: 500px;
        }
        #metaball{
            height: 500px;
            width: 500px;
        }
        #fog{
            width: 500px;
        }
     }
    }
@media screen and (min-width: 375px) {
   .majorText{font-size: 45px;}
   .minorText{font-size: 12px;}
   .middleText{font-size: 17px;}
}
@media screen and (min-width: 425px) {
   .majorText{font-size: 47px;}
   .minorText{font-size: 13px;}
   .middleText{font-size: 17px;}
}
@media screen and (min-width: 768px) {
    .majorText{font-size: 50px;}
    .minorText{font-size: 15px;}
    .middleText{font-size: 19px;}
.positionText{
    height: 100%;
    width: auto;
    top: 0;
    left: 5%;
}
.positionHeader{
    width: calc(50% - 10px);
}
.swiper{
    top: 0;
    left: 50%;
    width:  50%;
    height: 100%;
    grid-template-rows: 400px 10px;
}
.box{
    justify-content: end;
}
.container, .box{
    height: 500px;
}
#metaball{
    height: 500px;
    width: 500px;
}
#fog{
    width: 500px;
}
@media screen and (min-height: 620px){
    .box{bottom: 20%;}
}
@media screen and (min-height: 820px){
    .box{bottom: 30%;}
}
@media screen and (min-height: 1020px){
    .box{bottom: 35%;}
}
}
@media screen and (min-width: 1024px) {
    .majorText{font-size: 55px;}
    .minorText{font-size: 17px;}
    .middleText{font-size: 21px;}
    .box{
        justify-content: end;
        width: 95vw;
        right: 5%;
    }
    .container{
        max-width: 700px;
    }
    .container, .box{
        height: 700px;
    }
    #metaball{
        height: 700px;
        width: 700px;
    }
    #fog{
        width: 700px;
    }
    @media screen and (min-height: 620px){
        .box{bottom: 15%;}
    }
    @media screen and (min-height: 820px){
        .box{bottom: 20%;}
    }
    @media screen and (min-height: 1020px){
        .box{bottom: 25%;}
    }
}
@media screen and (min-width: 1440px) {
    .container{
        max-width: 800px;
    }
    .container, .box{
        height: 800px;
    }
    #metaball{
        height: 800px;
        width: 800px;
    }
    #fog{
        width: 800px;
    }
    @media screen and (min-height: 620px){
        .box{bottom: 10%;}
    }
    @media screen and (min-height: 820px){
        .box{bottom: 15%;}
    }
    @media screen and (min-height: 1020px){
        .box{bottom: 20%;}
    }
}