.block{
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
 }
.grid{
    display: grid;
    grid-template-rows: auto;
    row-gap: 15px;
    grid-column: 1fr;
    justify-content: center;
}
.head{
    width: 100%;
    text-align: center;
    justify-self: center;
    align-self: center;
}
.subhead{
    width: auto;
    justify-self: center;
    align-self: center;
}
.box{
    width: auto;
    justify-self: start;
    display: flex;
    align-items: center;
}
.box span{
    margin-left: 10px;
}