.box{
    position: absolute;
    bottom: 5%;
    width: 100vw;
    height: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.container{
    width: 100%;
    max-width: 600px;
    height: 300px;
    overflow-y: visible;
    overflow-x: visible;
    display: flex;
    align-items: center;
    justify-content: center; 
}